import Layout from "../../components/Layout/layout";
import styles from "./about.module.scss";

// FontAwesome
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faDownload } from "@fortawesome/free-solid-svg-icons";

const About = () => {
  return (
    <Layout title="About">
      <div className={styles.about}>
        <div className={styles.title}>About</div>
        <div className={styles.aboutContent}>
          <div className={styles.contLeft}>
            <div className={styles.introduce}>
              <div className={styles.introContent}>
                 <span>  </span>
                 .
                <br />
                        .
                <br />    
                <span>   </span>,
                <br />
                <span>Deep Dive</span>     .
              </div>
            </div>
            <div className={styles.education}>
              <span className={styles.eduTitle}>Education</span>
              <div className={styles.eduContent}>
                <div>
                  <p className={styles.eduCate}>
                    <span>2023.04</span>
                    <span className={styles.edu}>
                          ' React 
                      ' 
                    </span>
                  </p>
                  <p className={styles.eduCate}>
                    <span>2022.02</span>
                    <span className={styles.edu}>
                        |  () 
                    </span>
                  </p>
                  <p className={styles.eduCate}>
                    <span>2015.02</span>
                    <span className={styles.edu}> </span>
                  </p>
                </div>
              </div>
            </div>
            <div className={styles.resume}>
              <span className={styles.resumeTitle}>Resume</span>
              <a href={`${process.env.PUBLIC_URL}/resume.pdf`} download>
                <button>
                  <span> </span>
                  <FontAwesomeIcon icon={faDownload} />
                </button>
              </a>
            </div>
          </div>
          <div className={styles.contRight}>
            <span className={styles.name}></span>
            <span className={styles.info}>1996.06.10 |  </span>
            <img
              src={`${process.env.PUBLIC_URL}/images/profile.png`}
              alt="img"
            />
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default About;
