import { useState } from "react";
import Layout from "../../components/Layout/layout";
import styles from "./skills.module.scss";

interface ISkill {
  src: string;
  title: string;
  detail?: string[];
}

const Skills = () => {
  const list: ISkill[] = [
    {
      src: "react",
      title: "React",
      detail: [
        "useState, useEffect   Hooks            .",
        "React Router DOM       , SPA      .",
        "React        ,      .",
        "Axios   API           .",
      ],
    },
    {
      src: "typescript",
      title: "TypeScript",
      detail: [
        "type         .",
        "Interface       .",
        "      ,     .",
      ],
    },
    {
      src: "javascript",
      title: "JavaScript",
      detail: [
        "  ,            .",
        "React   JavaScript       .",
      ],
    },
    {
      src: "html5",
      title: "HTML5",
      detail: [
        "HTML         .",
        "HTML         .",
      ],
    },
    {
      src: "css3",
      title: "CSS3",
      detail: [
        "      .",
        "          .",
      ],
    },
  ];

  const list2 = [
    {
      src: "sass",
      title: "Sass",
      detail: [
        "Sass    mixin        .",
        "Sass     ,      .",
      ],
    },
    {
      src: "redux",
      title: "Redux",
      detail: [
        "Redux-toolkit          .",
        "Redux-persist     ,        .",
      ],
    },
    {
      src: "git",
      title: "Git",
      detail: [
        "Git    merge,       .",
        "GitKraken   Git     .",
      ],
    },
    {
      src: "nextjs",
      title: "Next.js",
      detail: [
        "Next.js  SSR       (SEO)     .",
        "Next.js      .",
      ],
    },
    {
      src: "firebase",
      title: "Firebase",
      detail: [
        "Firebase           .",
        "Firebase Database        .",
      ],
    },
  ];

  const [open, setOpen] = useState<string | null>(null);
  const [detail, setDetail] = useState<string[] | undefined>([]);

  const popup = (ele: ISkill) => {
    if (ele) {
      setDetail(ele.detail);
      setOpen(ele.title);
    }
  };

  const popdown = () => {
    setDetail([]);
    setOpen(null);
  };

  console.log(open, detail);

  return (
    <Layout title="Skills">
      <div className={styles.skill}>
        <div className={styles.title}>Skills</div>
        <div className={styles.skillBox}>
          <div className={styles.skillList}>
            {list.map((ele) => {
              return (
                <div
                  className={styles.skill}
                  key={ele.title}
                  onMouseEnter={() => popup(ele)}
                  onMouseLeave={popdown}
                >
                  <div className={styles.box}>
                    <img
                      src={`${process.env.PUBLIC_URL}/images/skills/${ele.src}.png`}
                      alt={ele.title}
                    />
                  </div>
                  <span>{ele.title}</span>
                  {open === ele.title && (
                    <div className={styles.modalTop}>
                      <span>{ele.title}</span>
                      <ul>
                        {ele.detail?.map((ele) => {
                          return <li key={ele}>{ele}</li>;
                        })}
                      </ul>
                    </div>
                  )}
                </div>
              );
            })}
          </div>
          <div className={styles.skillList}>
            {list2.map((ele) => {
              return (
                <div
                  className={styles.skill}
                  key={ele.title}
                  onMouseEnter={() => popup(ele)}
                  onMouseLeave={popdown}
                >
                  <div className={styles.box}>
                    <img
                      src={`${process.env.PUBLIC_URL}/images/skills/${ele.src}.png`}
                      alt={`${ele.title}`}
                    />
                  </div>
                  <span>{ele.title}</span>
                  {open === ele.title && (
                    <div className={styles.modalBot}>
                      <span>{ele.title}</span>
                      <ul>
                        {ele.detail?.map((ele) => {
                          return <li key={ele}>{ele}</li>;
                        })}
                      </ul>
                    </div>
                  )}
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Skills;
